import React from "react";

/**
 * @param {Int} tiempo Almacenar el tiempo
 */

let tiempo;

/**
 * @module Inactividad
 */

/**
 * Componente para la Inactividad del usuario en la pgina.
 * @memberof module:Inactividad
 * 
 * @returns {JSX}
 */

function Inactividad(){

    /**
     * @description Funcin que registra la actividad del usuario a travs del movimiento del ratn
     * @name RegistrarInactividad
     * @function
     * @memberof module:Inactividad
     * 
     */

    function RegistrarInactividad(){
        window.onload = Reiniciar;
        document.onmousemove = Reiniciar;
        document.onload = Reiniciar;
        document.onmousemove = Reiniciar;
    }
    
    /**
     * @description Funcin que hace que aparezca un mensaje cuando se pasa el tiempo y no ha habido movimiento de ratn.
     * @name MensajeInactividad
     * @function
     * @memberof module:Inactividad
     * 
     */

    function MensajeInactividad(){
        alert('Sigues ah?');
    }
    
    /**
     * @description Funcin que reinicia el tiempo y llama a MensajeInactividad para que aparezca el mensaje cuando haya pasado 2 minutos.
     * @name Reiniciar
     * @function 
     * @memberof module:Inactividad
     * 
     */

    function Reiniciar(){
        clearTimeout(tiempo);
        tiempo = setTimeout(MensajeInactividad, 120000)
    }
    return(
        RegistrarInactividad()
    )
}
export default Inactividad;